
import { AboutCompanyComponent } from '../pages/about-company/about-company.component';
import { ContactUsComponent } from '../pages/contact-us/contact-us.component';
import { EmailContactsComponent } from '../pages/email-contacts/email-contacts.component';
import { SuccessComponent } from '../pages/success/success.component';
import { EditClientComponent } from '../pages/edit-client/edit-client.component';
import { OktaCallbackComponent } from '@okta/okta-angular';
import { LoginAppComponent } from '../pages/login-app/login-app.component';
import { RegisterComponent } from '../pages/register/register.component';
import { ErrorPageComponent } from '../pages/error-page/error-page.component';

export const routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'home', component: AboutCompanyComponent },
  { path: 'customers', component: ContactUsComponent },
  { path: 'contact', component: EmailContactsComponent },
  { path: 'success', component: SuccessComponent },
  { path: 'edit-client', component: EditClientComponent },
  { path: 'implicit/callback', component: OktaCallbackComponent},
  { path: 'login',component: LoginAppComponent},
  { path: 'create',component: RegisterComponent},
  { path: 'error',component: ErrorPageComponent}
];
