import {Directive, ElementRef, HostListener, Input, ViewChild} from '@angular/core';
import {NgForm, NgModel} from '@angular/forms';

@Directive({
  selector: '[ngModel][appUpdateOnblur]',
  providers: [NgModel]
})
export class UpdateOnblurDirective {

  constructor(private el: ElementRef, private ngModel: NgModel) {
    if(!this.ngModel.options){
      this.ngModel.options = {};
    }
    this.ngModel.options.updateOn = 'blur';
  }
}
