import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from "@angular/router";
import {ClientService} from "../../service/client.service";
import {HttpClient,  HttpHeaders} from '@angular/common/http';

@Component({
  selector: 'edit-client',
  templateUrl: './edit-client.component.html',
  styleUrls: ['./edit-client.component.css']
})
export class EditClientComponent implements OnInit {

  customerID:any;

  constructor(private route: ActivatedRoute,private router: Router, private clientService : ClientService) {
    this.route.queryParams.subscribe(params => {
      this.customerID = params["ClientID"];
      });
   }

  contactInfo = {
    email:'',
    name:'',
    bookingDate:null,
    job:''
  }

  hairStore:any = [
    {'hairjob': 'Hair Cut', 'price':25},
    {'hairjob': 'Line Up', 'price':15},
    {'hairjob': 'Beard Clean Up', 'price':15}
  ]

  ngOnInit() {
    this.clientService.retrieveProfile(this.customerID).subscribe((response:any)=>{
      this.contactInfo.email = response.email;
      this.contactInfo.name = response.name;
      this.contactInfo.bookingDate = response.submitted_date;
    })
  }

  updateClient(){
    let httpsHeaders = new HttpHeaders();
    httpsHeaders.set('Content-Type', 'application/x-www-form-urlencoded');
    this.clientService.editAppointment(this.customerID, this.contactInfo, {headers:httpsHeaders,observe:'response'}).subscribe((data)=>{
      console.log('updating data:'+ data);
      if(data){
        console.log("successfully sent email");
        this.router.navigate(['success']);
      }else{
        console.log("opps something went wrong");
      }
    });;
  }

  disableEnterKey(event) {
    if (event.keyCode === 13) {
      event.preventDefault();
      //console.log('nice try');
      return false;
    }
  }

}
