import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ClientService {

  JwtToken:any;
  authToken:any;

  constructor(private _http: HttpClient) { }

  fetchClients() {
    return this._http.get("/client").map((response) =>{return response} );
  }

  retrieveProfile(id){
    return this._http.get("/client/"+id).map((response) =>{return response} );;
  }
  
  editAppointment(id:any, data:any,options:any){
    return this._http.put("/client/"+id,data,options);
  }

  deleteAppointment(data:any,options:any){
    return this._http.post("/client/save",data,options);
  }

  saveClient(data:any,options:any){
    return this._http.post("/client/save",data,options);
  }

  sendClientData(data:any,options:any){
    return this._http.post("/client/sendmail",data,options);
  }

  registerBarber(data:any,options:any){
    return this._http.post("/user/register",data,options);
  }

  login(data:any,options:any){
    return this._http.post("/user/login",data,options);
  }

  getClientDetails(id){
    return this._http.get("/client"+id);
  }

  storeUserData(token) {
    localStorage.setItem('jwt_token', token);
  }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }
 
}

