import { Component, OnInit,  } from '@angular/core';
import {Router} from "@angular/router";
import {HttpClient,  HttpHeaders} from '@angular/common/http';
import { ClientService } from "../../service/client.service";

@Component({
  selector: 'app-login-app',
  templateUrl: './login-app.component.html',
  styleUrls: ['./login-app.component.css']
})
export class LoginAppComponent implements OnInit {

  constructor(private _http: HttpClient, private router: Router,private clientService : ClientService) { }

  data_:any;
  alertMessage:String;
  showAlert:Boolean;

  barberInfo = {
    password:'',
    email: ''
  }

  ngOnInit(){
    this.showAlert = true;
  }

  login(){
    let httpsHeaders = new HttpHeaders();
    httpsHeaders.set('Content-Type', 'application/x-www-form-urlencoded');
    this.data_ = {
      'email':this.barberInfo.email,
      'password':this.barberInfo.password
  };
    

    this.clientService.login(this.data_,{headers:httpsHeaders,observe:'response'}).subscribe((data:any)=>{
      console.log(this.data_);
      if(data.body.token){
        this.clientService.storeUserData(data.body.token);
        console.log("successfully sent email");
        this.router.navigate(['home', {"valid":data.body.success}]);
      }else{
        this.showAlert = data.body.success;
        this.alertMessage = 'Invalid username or password';
        console.log("opps something went wrong");
      }
    })
  }

  disableEnterKey(event) {
    if (event.keyCode === 13) {
        event.preventDefault();
      return false;
    }
  }

}
